<template>
  <div class="create-post">
    <Panel>
      <template #header>
        <router-link to="/"></router-link><span>/</span
        ><span></span>
      </template>
      <template #content>
        <form action="">
          <div>
            <span>:</span>
            <select v-model="post.tab" name="" id="">
              <option value="ask"></option>
              <option value="share"></option>
              <option value="job"></option>
              <option value="dev"></option>
            </select>
          </div>
          <div>
            <input
              class="create-title"
              v-model="post.title"
              type="text"
              placeholder="10"
            />
          </div>
          <div>
            <mavon-editor
              defaultOpen="edit"
              :subfield="false"
              :toolbars="toolbars"
              v-model="post.content"
            />
          </div>
          <el-button @click="submit" type="primary" size="mini"></el-button>
        </form>
      </template>
    </Panel>
  </div>
</template>

<script>
import Panel from "../containers/Panel.vue";
import axios from "../plugins/axios";
export default {
  components: { Panel },
  data() {
    return {
      post: {
        title: "",
        tab: "",
        content: "",
      },
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        fullscreen: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: true, // eventssave
      },
    };
  },
  methods: {
    async submit() {
      //   
      let { tab, title, content } = this.post;
      // content = "<div class='markdown-tetx'>"+content+'</div>'

      // token
      const accesstoken = localStorage.getItem("token");
      const res = await axios.post("/topics", {
        tab,
        title,
        content,
        accesstoken,
      });
      // console.log(res);
      this.$router.push(`/PostContent/${res.topic_id}`);
    },
  },
};
</script>

<style>
.create-title{
  width: 98%;
  height: 26px;
  outline: none;
  margin-top: 5px;
}
</style>